<template>
  <div>
    <div class="bg">
      <div class="title">收藏资金</div>
      <div style="padding-bottom:20px;">
        <Input
          v-model="seachConfig.name"
          placeholder="资金名称"
          style="width: 200px;margin-right: 10px;"
        />
        <i-Select
          v-model="seachConfig.auType"
          style="width:130px;margin-right: 10px"
          placeholder="所有资金类型"
        >
          <i-Option value="-1">所有类型</i-Option>
          <i-Option value="1">个人资金</i-Option>
          <i-Option value="2">企业资金</i-Option>
          <i-Option value="3">政府资金</i-Option>
        </i-Select>
        <Button icon="ios-search" type="primary" @click="getData">搜 索</Button>
        <br />
      </div>
      <div class="list">
        <div>
          <Table :columns="columns1" :data="data">
            <template slot-scope="{ row, index }" slot="name">{{row.name}}</template>
            <template slot-scope="{ row, index }" slot="type">
              <Tag>{{row.auType == 1 ? '个人资金' : row.auType ==2 ? '企业资金' : row.auType == 3 ?'政府资金': '未知资金'}}</Tag>
            </template>
            <template slot-scope="{ row, index }" slot="caozuo">
              <Button @click="clickCancelCollect(row.code)" style="margin-right:10px;">取消收藏</Button>
              <Button :to="`/capital/detail/${row.moneyManage.id}`">查看资金</Button>
            </template>
          </Table>
        </div>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { cancelCollect } from "../../../api/public";
import {
  Icon,
  Button,
  Page,
  Poptip,
  Tag,
  Table,
  Select,
  Option,
  Input
} from "iview";
import { getCollect } from "../../../api/public";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Poptip.name]: Poptip,
    [Table.name]: Table,
    [Tag.name]: Tag,
    [Select.name]: Select,
    [Option.name]: Option,
    [Input.name]: Input,
    [Button.name]: Button
  },
  data() {
    return {
      //搜索配置
      seachConfig: {
        name: "",
        type: "2",
        auType: "-1",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      data: [],
      columns1: [
        {
          title: "资金名称",
          slot: "name"
        },
        {
          title: "资金类型",
          slot: "type",
          width: 150
        },
        {
          title: "收藏时间",
          key: "createTime",
          width: 150
        },
        {
          title: "操作",
          slot: "caozuo",
          width: 210
        }
      ]
    };
  },
  methods: {
    getData() {
      getCollect(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //取消收藏
    clickCancelCollect(code) {
      cancelCollect({ code, type: 2 }).then(res => {
        if (res.data.status == "SUCCESS") {
          this.getData();
        }
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    }
  },
  created() {
    this.getData();
  }
};
</script>
<style lang="less" scoped>
.bg {
  min-height: 600px;
  background: #ffffff;
  padding: 20px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    margin-bottom: 20px;
    color: #2868d1;
    font-size: 16px;
  }
  .list {
    .ivu-table-header {
      overflow: hidden;
      font-size: 14px;
      font-weight: 400;
    }
    .page {
      margin-top: 30px;
      padding-bottom: 60px;
      width: 100%;
      text-align: center;
    }
  }
}
</style>